
.content {
	width: 100%;
	transform: skew(0deg, -7deg) translateY(-230px);
	-ms-transform: skew(0deg, -7deg) translateY(-230px);
	-webkit-transform: skew(0deg, -7deg) translateY(-230px);
	-moz-transform: skew(0deg, -7deg) translateY(-190px); }

.container {
	@include flex-pack(space-around, false, wrap, auto);
	max-width: 1550px;
	margin: 0 auto;
	padding-top: 226px;
	transform: skew(0deg, 7deg);
	position: relative;
	@include r(1590) {
		margin: 0 20px; } }

.darker {
	background: #4A454B;
	color: #fff; }

.lighter {
	background: #4B4D4F; }

.white {
	background: #fff;
	color: black; }

.count-wrap {
	@include flex;
	justify-content: center;
	max-width: 1023px; }

.counter {
	@include flex;
	flex-basis: 100%;
	flex-wrap: wrap;
	box-sizing: border-box;
	justify-content: center;
	align-items: center;
	text-align: center;
	margin: 5em 0 5em;
	padding: 25px 70px;
	border: 1px solid #90D23D;
	border-radius: 15px; }

#time {
	display: block;
	width: 658px;
	font-size: 7em; }

#tips {
	@include flex;
	width: 100%;
	justify-content: space-between;
	padding: 0 135px;
	text-align: center;
	font-size: 1.5em;
	@include r(1630) {
		padding: 0 125px; } }

#tips span:nth-child(1) {
	margin-left: 25px; }

#tips span:nth-child(2) {
	margin-left: 19px; }

.images {
	@include flex;
	align-items: baseline;
	align-self: flex-start;
	margin-top: 4em;
	flex-basis: 82%;
	transform: skew(0deg, -9deg) translateY(25px); }

.images img {
	width: 12%;
	height: 12%; }

.images img:nth-child(1) {
	transform: translate(50%);
	z-index: 5; }

.images img:nth-child(2) {
	transform: translate(20%);
	z-index: 4; }

.images img:nth-child(3) {
	transform: translateY(-12%);
	z-index: 3; }

.images img:nth-child(4) {
	transform: translate(-15%);
	z-index: 2; }

.images img:nth-child(5) {
	transform: translate(-35%); }

.about {
	max-height: 30%;
